.product{
    width: 300px;
    padding: 1rem;
    background: #fff;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    border-radius: 8px;
    margin: 8px auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.product > img{
    height: 170px;
    border-radius: 8px;

}
.product__info > p{
    margin-bottom: 8x;
}

.info__price{
    font-weight: bold;
}
.info__name{
    font-size: 1rem;
    overflow: hidden;
}

.info__description {
    font-size: 0.8rem;
}

.info__button{
    display: block;
    width: 100%;
    text-decoration: none;
    text-align: center;
    color: #7D0707;
    background-color: #EAB72E;
    padding: 8px 16px;
    border-radius: 8px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    font-size: 1rem;
}
.info__button:hover{
    color: #EAB72E;
    background-color: #7D0707;
}
@media (max-width: 1232px){
    .product{
        width: 360px;
    }
}
@media (max-width: 1115px){
    .product{
        width: 330px;
    }
}
@media (max-width: 1028px){
    .product{
        width: 300px;
    }
}
@media (max-width: 950px){
    .product{
        width: 400px;
    }
}
@media (max-width: 830px){
    .product{
        width: 330px;
    }
}
@media (max-width: 700px){
