.cartitem{
    width: 100%;
    padding: 1rem;
    margin-bottom: 8px;
    display: grid;
    grid-template-columns: 1fr 4fr 1fr 1fr 1fr;
    gap: 8px;
    background: #fff;
    border-radius: 2px;
    place-items: center;
}
.cartitem__name{
    text-decoration: none;
    color: #171717;
}
.cartitem__name:hover{
    color: #EAB72E;
}
.cartitem__select{
    padding: 10px 17px;
}
.cartitem__delete{
   padding: 10px 17px; 
   color: #7D0707;
   background: #f4f4f4;
   cursor: pointer;
   transition: all 0.3s ease-out;
}
.cartitem__delete:hover,
.cartitem__delete:focus,
.cartitem__delete:active{
    background: #EAB72E;
    transform: scale(1.2);
}

@media (max-width: 700px) {
    .cartitem__name{
        font-size: 0.8rem;
    }
    .cartitem__select,
    .cartitem__delete{
        padding: 8px 13px;
    }
    .cartitem__price {
        font-size: 0.8rem;
    }
}
@media (max-width: 500px) {
    .cartitem__name{
        font-size: 0.6rem;
    }
    .cartitem__select,
    .cartitem__delete{
        padding: 5px 8px;
    }
    .cartitem__price {
        font-size: 0.6rem;
    }
}