.cartscreen{
    display: flex;
    max-width: 1300px;
    margin: 2rem auto;
    padding-top: 170px;
}
.cartscreen h2{
    margin-bottom: 1rem;
}
.cartscreen__left{
    flex: 0.7;
    margin-right: 1rem;
    background-color: transparent;
    padding: 1rem;
}
.cartscreen__right{
    flex: 0.3;
    background: #fff;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    height: fit-content;
}
.cartscreen__right div{
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding: 1rem;
}
.cartscreen__right div:last-child{
    border: none;
}
.cartscreen__info p {
    padding: 8px;
}
.cartscreen__right div button{
    padding: 10px 17px;
    width: 100%;
    background: #171717;
    color: #f4f4f4;
    border: 1px solid #171717;
    cursor: pointer;
}